import Vue from 'vue'
import VueCookies from 'vue-cookies'
import Router from 'vue-router'
import store from './store'

import { AuthAPI } from "@/scripts/ApiUtil"
import { DEVELOPER, MODERATOR, ADMINISTRATOR } from '../../server/UserRole'

Vue.use(VueCookies);
Vue.use(Router);

const router = new Router({
	routes: [
		{
			path: "/",
			name: "Dashboard",
			component: () => import(/* webpackChunkName: "dashboard" */ './views/Dashboard.vue')
		},
		{
			path: '/login',
			name: 'login',
			component: () => import(/* webpackChunkName: "login" */ './views/Login.vue'),
			meta: {
				noAuth: true
			}
		},
		{
			path: '/register',
			name: 'register',
			component: () => import(/* webpackChunkName: "register" */ './views/Register.vue'),
			meta: {
				noAuth: true
			}
		},
		{
			path: "/packages",
			name: "packages",
			component: () => import(/* webpackChunkName: "packages" */ './views/Packages.vue'),
			meta: {
				minimumRole: DEVELOPER
			}
		},
		{
			path: "/packages/new",
			name: "packageAdd",
			component: () => import(/* webpackChunkName: "packageAdd" */ './views/PackageAdd.vue'),
			meta: {
				minimumRole: DEVELOPER
			}
		},
		{
			path: "/packages/:packageId",
			name: "packageEdit",
			component: () => import(/* webpackChunkName: "packageEdit" */ './views/PackageEdit.vue'),
			meta: {
				minimumRole: DEVELOPER
			}
		},
		{
			path: "/packages/:packageId/versions/add",
			name: "versionAdd",
			component: () => import(/* webpackChunkName: "versionAdd" */ './views/VersionAdd.vue'),
			meta: {
				minimumRole: DEVELOPER
			}
		},
		{
			path: "/packages/:packageId/versions/:versionId",
			name: "versionEdit",
			component: () => import(/* webpackChunkName: "versionEdit" */ './views/VersionEdit.vue'),
			meta: {
				minimumRole: DEVELOPER
			}
		},
		
		{
			path: "/error/:errorId",
			name: "error",
			component: () => import(/* webpackChunkName: "error" */ './views/Error.vue'),
			meta: {
				noAuth: true
			}
		},
		{ path: '*', redirect: '/error/404' },
	],
	linkActiveClass: "active"
});

router.beforeEach(async (to, from, next) => {
	if (!to.matched.some(_ => _.meta.noAuth)) {
		if (!window.$cookies.get("authToken")) {
			return next({
				path: "/login",
				query: {
					next: to.path != "/" ? to.path : undefined
				},
				replace: true
			});
		} else {
			const authData = await AuthAPI.verify();
			if (authData.status != 200) {
				return next({
					path: "/login",
					query: {
						next: to.path != "/" ? to.path : undefined
					},
					replace: true
				});
			} else {
				store.commit("setUserRole", authData.data.role)
			}
		}
	}
	
	if (to.matched.some(_ => _.meta.minimumRole)) {
		let minimumRole = to.matched.map(_ => _.meta.minimumRole)[0];
		if ((store.getters.userRole & minimumRole) != minimumRole) {
			return next({
				path: "/error/403",
				replace: true
			})
		}
		return next();
	}
	
	next();
});

export default router;
